<template>
	<view class="page">
		<scroll-view class="scrollView" scroll-y="true">
			<view class="feed_head">
				<view class="feed_head_type">
					<view class="itemView_title" style="margin-top: 32rpx;">请选择意见反馈的类型</view>
					<view class="itemView_content">
						<view class="itemView_content_item" v-for="(item,index) in list" :key="index"
							@click="click(index)">
							<button :class="item.select ? 'selectButton': 'button'" plain="false">{{item.name}}</button>
						</view>
					</view>
				</view>
				<view class="feed_head_content">
					<view class="itemView_title" style="margin-top: 52rpx;">反馈内容</view>
					<u-textarea class="itemView_textarea" v-model="feedContent" placeholder="请输入描述，投诉举报将在7个工作日内处理"
						height="240rpx"></u-textarea>
				</view>
				<view class="feed_head_photo" @click="uploadImage">
					<view class="itemView_title" style="margin-top: 48rpx;">上传问题截图</view>
					<view class="itemView_add" v-if="!feedPhoto">
						<image src="../../static/photo_add@2x.png"></image>
					</view>
					<image class="itemView_img" v-else="feedPhoto" :src="imageUrl"></image>
				</view>
				<view class="feed_head_phone">
					<view class="itemView_title" style="margin-top: 48rpx;">联系方式</view>
					<u-input class="itemView_input" v-model="feedPhone" placeholder="请输入您的联系方式"></u-input>
				</view>
			</view>
		</scroll-view>
		<view class="feed_bottom">
			<button :class="isSub ? 'button_sub':'button'" plain="false" @click="subClick">提交</button>
		</view>
	</view>
</template>

<script>
	import Api from "@/utils/api.js"
	import httpApi from '@/api/aili/index.js';
	import Upyun from "@/utils/upyun.js"
	import Upload from "@/utils/plugins.js"
	import pp from "@/utils/public.js"
	import upload from "@/utils/uploadImg.js"
	export default {
		data() {
			return {
				feedType: [],
				feedContent: null,
				feedPhoto: null,
				feedPhone: null,
				imageUrl: null,
				list: [{
					id: "1",
					name: "BUG",
					select: false
				}, {
					id: "2",
					name: "咨询",
					select: false
				}, {
					id: "3",
					name: "建议",
					select: false
				}, {
					id: "4",
					name: "吐槽",
					select: false
				}]
			}
		},
		computed: {
			isSub() {
				return this.feedType.length > 0 && this.feedContent
			}
		},
		onLoad() {
			pp.test()
		},
		methods: {
			click(idx) {
				console.log("点击了第", idx)
				this.list[idx].select = !this.list[idx].select
				this.feedType = this.list.filter(item => item.select === true)
			},

			uploadImage() {
				upload.uploadImg().then(res => {
					debugger
					if (res.code == 200) {
						if (res.url) {
							this.$u.toast("上传成功")
							this.feedPhoto = res.url
							this.imageUrl = this.$config.imgUrl + res.url
							this.$forceUpdate()
						}
					}
				})
			},

			// 修改头像 UPYUN yssj@91kwd.com :ZtIEQ887ZvJLTMfAzqiSrpgxIc8=
			editAvatar() {
				let that = this
				console.log("又拍云", pp)
				uni.chooseImage({
					count: 1,
					sourceType: ['album', 'camera'], //从相册选择
					success: (res) => {
						const imageSrc = res.tempFilePaths[0]
						pp.upload({
							localPath: imageSrc,
							remotePath: "/user/feedBack/1587985/", //上传文件保存路径
							success: function(res) {
								console.log('uploadImage success, res is:', res.data)
								if (res.statusCode == 200) {
									let avatarInfo = JSON.parse(res.data).url
									console.log("上传的图片=", avatarInfo)
									that.avatar = 'http://img.xxxxxxxx.com' + avatarInfo
								}
							},
							fail: function({
								errMsg
							}) {
								console.log('uploadImage fail, errMsg is111111', errMsg)
							},
						})
						console.log("imageSrc=", imageSrc)
						// var date = new Date().toGMTString()
						// let token = uni.getStorageSync(this.$config.storage.token)
						// uni.uploadFile({
						// 	//这里的url不用修改，不可以修改；直接用即可，这是官网的接收地址，不要修改；
						// 	url: `https://v0.api.upyun.com/yssj668`,
						// 	filePath: imageSrc,
						// 	name: 'file',
						// 	header: {
						// 		Authorization: `Bearer ${token}`
						// 		// "authorization": 'UPYUN yssj:razonoSO4yJMPneAng8B2KM97F4=',
						// 		// "Date": date,
						// 		// "Content-Type": "application/x-www-form-urlencoded",
						// 	},
						// 	formData: {
						// 		"authorization": 'UPYUN yssj:razonoSO4yJMPneAng8B2KM97F4=',
						// 		"policy": 'eyJidWNrZXQiOiIveXNzajY2OCIsInNhdmUta2V5IjoiYWlsaS91cGxvYWRfe3JhbmRvbTMyfXsuc3VmZml4fSIsImV4cGlyYXRpb24iOjE3MDAyNjk5MjF9',
						// 	},

						// 	success: (uploadFileRes) => {
						// 		console.log("上传成功", uploadFileRes.data);
						// 	},
						// 	fail(e) {
						// 		console.log("上传失败", e);
						// 	}
						// })
					},
				})
			},

			subClick() {

				if (this.feedType.length === 0) {
					this.$u.toast("请选择反馈类型")
					return
				}
				if (!this.feedContent) {
					this.$u.toast("请输入内容")
					return
				}

				let userInfo = uni.getStorageSync(this.$config.storage.userInfo)
				this.$http.get(httpApi.feedBack, {
					params: {
						content: this.feedContent,
						phone: this.feedPhone,
						type: this.feedType,
						photo: this.feedPhoto,
						userid: userInfo["user_id"],
						version_no: "3.8.7"
					}
				}).then(res => {
					console.log("请求结果=", res)
					if (res.status === "1") {
						this.$u.toast("操作成功")
					} else {
						this.$u.toast(res.message)
					}
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	.page {
		height: calc(100vh);
		background-color: #fff;
	}

	.scrollView {
		height: calc(100vh - 228rpx);
	}

	.feed_head {
		display: flex;
		flex-direction: column;
		margin-left: 40rpx;
		margin-right: 40rpx;

		&_type {
			width: 100%;
			display: flex;
			flex-direction: column;

			.itemView_grid {
				margin-top: 24rpx;

				.button {
					width: 144rpx;
					height: 68rpx;
					background: #F0F2F5;
					border-radius: 34rpx;
					border-width: 0px;
					line-height: 68rpx;
					font-size: 24rpx;
					font-family: PingFangSC-Regular, PingFang SC;
					font-weight: 400;
					color: #272424;
				}

				.selectButton {
					width: 144rpx;
					height: 68rpx;
					background: #EC3A76;
					border-radius: 34rpx;
					border-width: 0px;
					line-height: 68rpx;
					font-size: 24rpx;
					font-family: PingFangSC-Regular, PingFang SC;
					font-weight: 400;
					color: #fff;
				}
			}
		}

		&_content {
			width: 100%;
			display: flex;
			flex-direction: column;

			.itemView_textarea {
				margin-top: 24rpx;
			}
		}

		&_photo {
			width: 100%;
			display: flex;
			flex-direction: column;

			.itemView_add {
				width: 200rpx;
				height: 200rpx;
				margin-top: 24rpx;
				border-radius: 8rpx;
				border: 2rpx solid #E0E0E0;
				display: flex;
				align-items: center;
				justify-content: center;

				image {
					width: 28rpx;
					height: 28rpx;
				}
			}

			.itemView_img {
				width: 200rpx;
				height: 200rpx;
				margin-top: 24rpx;
			}
		}

		&_phone {
			width: 100%;
			display: flex;
			flex-direction: column;

			.itemView_input {
				margin-top: 24rpx;
			}
		}

		.itemView_title {
			height: 40rpx;
			font-size: 28rpx;
			font-family: PingFangSC-Medium, PingFang SC;
			font-weight: 500;
			color: #272424;
			line-height: 40rpx;
			justify-content: center;
		}

		.itemView_content {
			width: 100%;
			display: flex;
			flex-direction: row;
			align-items: center;
			justify-content: center;
			margin-top: 24rpx;

			&_item {
				width: auto;
				flex: 1;

				.button {
					width: 144rpx;
					height: 68rpx;
					background: #F0F2F5;
					border-radius: 34rpx;
					border-width: 0px;
					line-height: 68rpx;
					font-size: 24rpx;
					font-family: PingFangSC-Regular, PingFang SC;
					font-weight: 400;
					color: #272424;
				}

				.selectButton {
					width: 144rpx;
					height: 68rpx;
					background: #EC3A76;
					border-radius: 34rpx;
					border-width: 0px;
					line-height: 68rpx;
					font-size: 24rpx;
					font-family: PingFangSC-Regular, PingFang SC;
					font-weight: 400;
					color: #fff;
				}
			}
		}
	}

	.feed_bottom {
		height: 228rpx;
		width: 100%;
		position: absolute;
		bottom: 0;
		display: flex;
		align-items: center;
		background-color: #fff;

		.button {
			width: 670rpx;
			height: 76rpx;
			background: #E0E0E0;
			border-radius: 52rpx;
			border-width: 0px;
			font-size: 28rpx;
			font-family: PingFangSC-Medium, PingFang SC;
			font-weight: 500;
			color: #858585;
			line-height: 76rpx;
		}

		.button_sub {
			width: 670rpx;
			height: 76rpx;
			background: #EC3A76;
			box-shadow: 0rpx 4rpx 20rpx 0rpx rgba(95, 99, 105, 0.18), 0rpx 6rpx 14rpx 0rpx rgba(255, 25, 111, 0.36);
			border-radius: 52rpx;
			border-width: 0px;
			font-size: 28rpx;
			font-family: PingFangSC-Medium, PingFang SC;
			font-weight: 500;
			color: #272424;
			line-height: 76rpx;
			text-shadow: 0px 4px 20px rgba(95, 99, 105, 0.18);
		}
	}
</style>